body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

body {
  padding: 0;
  margin: 0;
}
.navbar {
    background: #0C2D48;
    height: 8vh;
    width: 100%;
    position: fixed;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
}

.logo {
    color: white;
    font-size: 60px;
    font-weight: 100;
    padding-left: 5vw;
}

#open .logo {
    display: none;
}

.links {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: horizontal;
    height: 100%;
    width: 50%;
}

.links a {
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
}

#open {
    height: 100vh;
    width: 70vw;
    margin-left: 30vw;
}

#open .links {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#open .links a {
    padding-bottom: 40px;
}

.language {
    margin-left: 28vw;
    font-size: 10px;
    font-weight: 500;
    cursor: pointer;
    background-color: #ffffff60;
    color: #0C2D48;
    padding: 2px 5px;
    border-radius: 5px;
    min-width: 7.5vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.language .language_flag {
    width: 20px;
    padding-right: 5px;
}

.language:hover {
    color: white;
    background-color: #ffffff80;
}

#open .language {
    display: none;
}

.toggleButton {
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.toggleButton button{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
    margin-right: 10px;
}

.toggleButton svg {
    font-size: 40px;
    height: 100%;
}

#open .toggleButton {
    position: absolute;
    top: 0;
    right: 0;
    height: 10vh;
}

@media screen and (min-width: 600px) {
    #close .toggleButton {
        display: none;
    }
}

@media screen and (min-width: 600px) {

    .language {
        min-width: 7.5vw;
    }

}

@media screen and (min-width: 900px) {

    .language {
        min-width: 4vw;
    }

}

@media screen and (max-width: 600px) {
    #close .links {
        display: none;
    }

    .language {
        margin-left: 40vw;
    }
}
.home {
    padding-top: 8vh;
}


/* https://www.canva.com/colors/color-palettes/mermaid-lagoon/ */
.area {
    /* background-color: red; */
    padding: 0;
    margin: 0;
    text-align: left;
    /* background-color: #B1D4E030; */
    position: relative;
    height: 40vh;
    padding-bottom: 5vh;
}

.intro_background {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    object-fit: cover;
    object-position: 50% 25%;
    -webkit-filter: grayscale(20%);
            filter: grayscale(20%);
    opacity: 0.4;
}


.content {
    padding-top: 3vh;
    /* padding-bottom: 10vh; */
    display: flex;
    flex-direction: column;
    padding-left: 5vw;
    justify-content: center;
    width: 90vw;
    min-height: 300px;
    z-index: 0;
    position: absolute;
}

.content .main {
    font-weight: 500;
    margin-bottom: 0;
    color: #0C2D48;
    
}

.content .character {
    color: rgb(27, 44, 36);
    font-weight: 800;
    font-size: 18px;
    color: #0C2D48;
}

.content .desc {
    margin-top: 1vh;
    font-size: 16px;
    line-height: 24px;
}

@media screen and (max-width: 900px) {
    .content .desc {
        font-size: 14px;
    }
}
.footer {
    background: #0C2D48;
    color: white;
    display: flex;
    flex-direction: column;
    font-size: 14px;
}
.project_card {
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    width: 260px;
    height: 310px;
    border-radius: 0.5rem;
    overflow: hidden;
    background-color: #2E8BC010;
    transition: 0.5s;
    margin-bottom: 2vh;
}

div.project_card:hover {
    scale: 1.05;
    background-color: #2E8BC015;
}

.project_card .text_area {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 43%;
}

.project_card .look {
    margin-bottom: 1vh;
    font-size: 14px;
    font-weight: 300;
    width: 95%;
    background-color: #0C2D4895;
    border: none;
    padding: 3px;
    color: white;
    border-radius: 3px;
}

.project_card button.look:hover {
    background-color: #0C2D48;
}

.project_card .desc {
    top: 0;
    width: 100%;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    width: 90%;
    line-height: 20px;
    /* display: none; */
}



.project_card div.desc:hover {
    opacity: 1;
}



.project_card .card_image {
    width: 93%;
    height: 50%;
    position: absolute;
    top: 2%;
    object-fit: cover;
    object-position: top;
    /* border-radius: 0.5em; */
    opacity: 0.6;
    /* aspect-ratio: '3/2'; */
}

div.project_card:hover .card_image {
    opacity: 1;
}

 
@media screen and (max-width: 600px) {
    
    .project_card {
        width: 350px;
        height: 360px;
    }
    
    .project_card .text_area {
        height: 30%;
    }

    .project_card .card_image {
        height: 60%;
    }
}
.projects {
    background-color: #145DA015;
    padding-top: 8vh;
    padding-bottom: 5vh;
    padding-left: 5vw;
}

.projects_title {
    font-size: 18px;
    margin-bottom: 0;
    padding-bottom: 0;
    text-align: left;
    font-weight: 800;
    color: gray;
    letter-spacing: 14px;
}

.projects_panel {
    padding-top: 3vh;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}

@media screen and (max-width: 1500px) {
    .projects_panel {
        padding-top: 3vh;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
}

@media screen and (max-width: 1200px) {
    .projects_panel {
        padding-top: 3vh;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 900px) {
    .projects_panel {
        padding-top: 3vh;
        padding-right: 5vw;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;

    }
}

@media screen and (max-width: 600px) {
    .projects_panel {
        padding-top: 3vh;
        padding-right: 5vw;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;

    }
}
.skills {
    background-color: #B1D4E030;
    padding-top: 8vh;
    padding-bottom: 5vh;
    text-align: left;
    padding-left: 5vw;
    
}

.skills .title {
    font-size: 18px;
    margin-bottom: 0;
    padding-bottom: 3vh;
    font-weight: 800;
    color: gray;
    letter-spacing: 14px;
}

.skills .skill_section {
    display: flex;
    flex-direction: row;
}

.skills .skill_section .icon_img {
    /* width: 30px; */
    height: 50px;
    /* position: absolute; */
}

.skills .skill_section .text_area {
    display: flex;
    flex-direction: column;
}

.skills .category {
    font-size: 18px;
    font-weight: 800;
    padding-bottom: 1vh;
    padding-left: 5vw;
    /* width: ; */

}

.skills .desc {
    padding-bottom: 5vh;
    font-size: 16px;
    padding-left: 5vw;
}

.skills .desc, .title, .category {
    /* width: 90vw; */
}

@media screen and (max-width: 900px) {
    .skills .desc {
        font-size: 14px;
    }
}
